___CSS_LOADER_EXPORT___.push([module.id, `/* Responsive */

@media screen and (min-width: 455px) {
    .about-content {
      padding: 16px 47px;
    }
  
    .about-title {
      font-size: 28px;
    }
  
    .about-description {
      font-size: 16px;
    }
}

  @media screen and (min-width: 650px) {
    .about-content {
      margin: 0 auto;
      max-width: 650px;
    }
}

/* @media  screen and (min-width: 769px) {
    .artikel-space {
        padding-top: 5rem;
    }
} */

/* @media screen and (min-width: 835px) {
    .artikel-space {
        padding-top: 6rem;
    }
} */

@media screen and (min-width: 900px) {
    .about-articel{
        display: grid;
        grid-template-columns: 1fr 1fr;
    }
    .about-content {
      padding: 16px 32px;
    }
    .hamburger {
        display: none;
    }
    /* .post-content {
        margin-top: 3rem !important;
    } */
} 

@media screen and (max-width: 1060px){
    .container {
        margin: 0 auto;
        width: 95%;
    }
    .home-text {
        width: 100%;
    }
}

@media screen and (max-width: 900px){
    #select-tab {
        align-items: center;
    }
    .post-container {
        margin: 0 auto;
        width: 90%;
    }
    
    .post-content {
        margin-top: 3rem !important;
    }
}

@media screen and (max-width: 834px){
    .nav {
        padding: 14px 0;
    }
}

@media screen and (max-width: 768px){
    .BgColour {
        color: none;
        transition: none;
        background-color: none;
        border-bottom: none;
        width: 0;
    }
    header {
        position:unset;
    }
    .hamburger {
        display: block;
    }

    .hamburger.active .bar:nth-child(2){
        opacity: 0;
    }
    .hamburger.active .bar:nth-child(1){
        transform: translate(8px) rotate(45deg);
    }
    .hamburger.active .bar:nth-child(3){
        transform: translate(-8px) rotate(-45deg);
    }

    .nav-list {
        position: absolute;
        left: -100%;
        top: 70px;
        gap: 0;
        flex-direction: column;
        background-color: #23295c;
        width: 100%;
        text-align: center;
        transition: 0.3s;
        padding: 14px;
        height: 100;
    }

    .nav-list li {
        margin: 16px;
    }

    .nav-list.active {
        left: 0;
    }

    .nav {
        padding: 10px 0;
        min-width: 44px;
    }
    section {
        padding: 2rem 0;
    }
    .header-content {
        margin-top: 3rem !important;
    }
    .home {
        min-height: 380px;
    }
    .home-title {
        font-size: 3rem;
    }
    .header-title {
        font-size: 1.8rem;
    }
    .header-img {
        height: 370px;
    }
    .post-header {
        height: 435px;
    }
}

@media screen and (max-width: 603px){
    .post-header {
        height: 390px;
    }
    .header-title {
        width: 100%;
        font-size: 1.5rem;
    }
    .header-img {
        height: 340px;
    }
    .footer {
        flex-direction: column;
        row-gap: 1rem;
        padding: 20px 0;
        text-align: center;
    }
    
}

@media screen and (max-width: 523px){
    .home-title {
        font-size: 2rem;
    }
}

@media screen and (max-width: 457px){
    .home-title {
        font-size: 2rem;
    }
    .home-subtitle {
        font-size: 0.9rem;
    }
    .home {
        min-height: 300px;
    }
    .post-box {
        padding: 10px;
    }
    .header-title {
        width: 90%;
        font-size: 1.2rem;
    }
}

@media screen and (max-width: 365px){
    .home-title {
        font-size: 1.8rem;
    }
    .home-subtitle {
        font-size: 0.7rem;
    }
    .header-title {
        width: 90%;
        font-size: 1rem;
    }
}

@media (max-width: 768px) {
    .main-card .cards .card{
      margin: 20px 0 10px 0;
    }
    header {
        background-color: var(--container-color);
    }
}
@media (max-width: 600px) {
    .main-card .cards .card{
      width: 100%;
    }
}

@media (min-width: 888px) {
    .main-card {
        padding: 0 24px;
    }
    
}

@media (min-width: 584px) {
    .card {
        max-width: 280px;
    }
}`, "",{"version":3,"sources":["webpack://./src/styles/responsive.css"],"names":[],"mappings":"AAAA,eAAe;;AAEf;IACI;MACE,kBAAkB;IACpB;;IAEA;MACE,eAAe;IACjB;;IAEA;MACE,eAAe;IACjB;AACJ;;EAEE;IACE;MACE,cAAc;MACd,gBAAgB;IAClB;AACJ;;AAEA;;;;GAIG;;AAEH;;;;GAIG;;AAEH;IACI;QACI,aAAa;QACb,8BAA8B;IAClC;IACA;MACE,kBAAkB;IACpB;IACA;QACI,aAAa;IACjB;IACA;;OAEG;AACP;;AAEA;IACI;QACI,cAAc;QACd,UAAU;IACd;IACA;QACI,WAAW;IACf;AACJ;;AAEA;IACI;QACI,mBAAmB;IACvB;IACA;QACI,cAAc;QACd,UAAU;IACd;;IAEA;QACI,2BAA2B;IAC/B;AACJ;;AAEA;IACI;QACI,eAAe;IACnB;AACJ;;AAEA;IACI;QACI,WAAW;QACX,gBAAgB;QAChB,sBAAsB;QACtB,mBAAmB;QACnB,QAAQ;IACZ;IACA;QACI,cAAc;IAClB;IACA;QACI,cAAc;IAClB;;IAEA;QACI,UAAU;IACd;IACA;QACI,uCAAuC;IAC3C;IACA;QACI,yCAAyC;IAC7C;;IAEA;QACI,kBAAkB;QAClB,WAAW;QACX,SAAS;QACT,MAAM;QACN,sBAAsB;QACtB,yBAAyB;QACzB,WAAW;QACX,kBAAkB;QAClB,gBAAgB;QAChB,aAAa;QACb,WAAW;IACf;;IAEA;QACI,YAAY;IAChB;;IAEA;QACI,OAAO;IACX;;IAEA;QACI,eAAe;QACf,eAAe;IACnB;IACA;QACI,eAAe;IACnB;IACA;QACI,2BAA2B;IAC/B;IACA;QACI,iBAAiB;IACrB;IACA;QACI,eAAe;IACnB;IACA;QACI,iBAAiB;IACrB;IACA;QACI,aAAa;IACjB;IACA;QACI,aAAa;IACjB;AACJ;;AAEA;IACI;QACI,aAAa;IACjB;IACA;QACI,WAAW;QACX,iBAAiB;IACrB;IACA;QACI,aAAa;IACjB;IACA;QACI,sBAAsB;QACtB,aAAa;QACb,eAAe;QACf,kBAAkB;IACtB;;AAEJ;;AAEA;IACI;QACI,eAAe;IACnB;AACJ;;AAEA;IACI;QACI,eAAe;IACnB;IACA;QACI,iBAAiB;IACrB;IACA;QACI,iBAAiB;IACrB;IACA;QACI,aAAa;IACjB;IACA;QACI,UAAU;QACV,iBAAiB;IACrB;AACJ;;AAEA;IACI;QACI,iBAAiB;IACrB;IACA;QACI,iBAAiB;IACrB;IACA;QACI,UAAU;QACV,eAAe;IACnB;AACJ;;AAEA;IACI;MACE,qBAAqB;IACvB;IACA;QACI,wCAAwC;IAC5C;AACJ;AACA;IACI;MACE,WAAW;IACb;AACJ;;AAEA;IACI;QACI,eAAe;IACnB;;AAEJ;;AAEA;IACI;QACI,gBAAgB;IACpB;AACJ","sourcesContent":["/* Responsive */\r\n\r\n@media screen and (min-width: 455px) {\r\n    .about-content {\r\n      padding: 16px 47px;\r\n    }\r\n  \r\n    .about-title {\r\n      font-size: 28px;\r\n    }\r\n  \r\n    .about-description {\r\n      font-size: 16px;\r\n    }\r\n}\r\n\r\n  @media screen and (min-width: 650px) {\r\n    .about-content {\r\n      