n\r\n.nav-list {\r\n    display: flex;\r\n}\r\n\r\n.nav-btn {\r\n    padding: 8px 14px;\r\n    text-transform: uppercase;\r\n    font-weight: 500;\r\n    color: var(--bg-color);\r\n}\r\n\r\n.nav-btn:hover {\r\n    text-decoration: overline;\r\n    transition: 0.3s;\r\n}\r\n\r\n.BgColour {\r\n    color: black;\r\n    transition: all 0.5s;\r\n    background-color: white;\r\n    border-bottom: 1px solid rgb(241, 241, 241);\r\n    width: 100%;\r\n}\r\n\r\n/* Drawer */\r\n.hamburger {\r\n    display: none;\r\n    cursor: pointer;\r\n    background: none;\r\n    border: none;\r\n}\r\n\r\n.bar {\r\n    display: block;\r\n    width: 25px;\r\n    height: 3px;\r\n    margin: 5px auto;\r\n    -webkit-transition: all 0.3s ease-in-out;\r\n    transition: all 0.3s ease-in-out;\r\n    background-color: white;\r\n}\r\n\r\n\r\n/* Home */\r\n.home {\r\n    width: 100%;\r\n    min-height: 440px;\r\n    /* background: var(--container-color); */\r\n    background:linear-gradient(var(--container-color), rgba(12, 23, 172, 0.603)), \r\n    url(\"/dist/img/hero.png\") no-repeat center / cover;\r\n    display: grid;\r\n    justify-content: center;\r\n    align-items: center;\r\n}\r\n\r\n.home-text {\r\n    color: var(--bg-color);\r\n    text-align: center;\r\n}\r\n\r\n.home-title {\r\n    font-size: 3.5rem;\r\n    user-select: text;\r\n}\r\n\r\n.home-subtitle {\r\n    font-size: 1rem;\r\n    font-weight: 400;\r\n}\r\n\r\n/* About */\r\n.about {\r\n    width: 100%;\r\n    min-height: 440px;\r\n    background: var(--bg-color);\r\n    display: grid;\r\n    justify-content: center;\r\n    align-items: center;\r\n    padding: 0;\r\n}\r\n\r\n.about-figure {\r\n    width: 100%;\r\n}\r\n\r\n.about-figure img {\r\n    width: 100%;\r\n    max-height: 185px;\r\n    padding-top: 32px;\r\n}\r\n\r\n.about-figure figcaption {\r\n    text-align: center;\r\n    color: #666666;\r\n    font-size: 13px;\r\n    font-weight: 400;\r\n    margin-top: 8px;\r\n}\r\n\r\n.about-content {\r\n    width: 100%;\r\n    padding: 16px 32px;\r\n}\r\n  \r\n.about-description {\r\n    font-size: 16px;\r\n    margin-top: 12px;\r\n    /* To Remain Title In 3 */\r\n    /* display: -webkit-box;\r\n    -webkit-line-clamp: 3;\r\n    -webkit-box-orient: vertical;\r\n    overflow: hidden; */\r\n}\r\n\r\n.about-button {\r\n    font-family: \"Roboto\", sans-serif;\r\n    text-transform: uppercase;\r\n    margin-top: 24px;\r\n    cursor: pointer;\r\n    padding: 8px 16px;\r\n    color: white;\r\n    border: none;\r\n    border-radius: 5px;\r\n    background-color: #2c3e50;\r\n    transition: opacity 0.3s;\r\n}\r\n\r\n.about-button:hover {\r\n    opacity: 0.8;\r\n}\r\n\r\n/* space */\r\n.artikel-space {\r\n    font-size: 2rem;\r\n    font-weight: 600;\r\n    text-align: center;\r\n    margin-top: 24px;\r\n    color: var(--container-color);\r\n}\r\n\r\n/* Posts */\r\n.post {\r\n    display: grid;\r\n    grid-template-columns: repeat(auto-fit, minmax(280px, auto));\r\n    justify-content: center;\r\n    gap: 1.5rem;\r\n}\r\n\r\n.post-box {\r\n    background: var(--bg-color);\r\n    box-shadow: 0 4px 14px hsl(355deg 25% 15% / 10%);\r\n    padding: 15px;\r\n    border-radius: 0.5rem;\r\n}\r\n\r\n.post-img {\r\n    width: 100%;\r\n    height: 200px;\r\n    object-fit: cover;\r\n    object-position: center;\r\n    border-radius: 0.5rem;\r\n}\r\n\r\n.category {\r\n    font-size: 0.9rem;\r\n    font-weight: 500;\r\n    text-transform: uppercase;\r\n    color: var(--container-color);\r\n}\r\n\r\n.post-title {\r\n    font-size: 1.3rem;\r\n    font-weight: 600;\r\n    color: var(--text-color);\r\n    /* To Remain Title In 2 */\r\n    display: -webkit-box;\r\n    -webkit-line-clamp: 2;\r\n    -webkit-box-orient: vertical;\r\n    overflow: hidden;\r\n}\r\n\r\n.post-date {\r\n    display: flex;\r\n    font-size: 0.875rem;\r\n    text-transform: uppercase;\r\n    font-weight: 400;\r\n    margin-top: 4px;\r\n}\r\n\r\n.post-description {\r\n    font-size: 0.9rem;\r\n    line-height: 1.5rem;\r\n    margin: 5px 0 10px;\r\n    /* To Remain Title In 3 */\r\n    display: -webkit-box;\r\n    -webkit-line-clamp: 3;\r\n    -webkit-box-orient: vertical;\r\n    overflow: hidden;\r\n}\r\n\r\n/* Footer */\r\n.footer {\r\n    position: relative;\r\n    display: flex;\r\n    justify-content: space-between;\r\n    align-items: center;\r\n    padding: 30px 0;\r\n}\r\n\r\n.footer p {\r\n    font-size: 0.938rem;\r\n}\r\n\r\n.social {\r\n    display: flex;\r\n    align-items: center;\r\n    column-gap: 1rem;\r\n}\r\n\r\n.social .bx {\r\n    font-size: 1.4rem;\r\n    margin: 11px;\r\n    color: var(--text-color);\r\n}\r\n\r\n.social .bx:hover {\r\n    color: #4e5de2;\r\n    transition: 0.3s all linear;\r\n}\r\n\r\n/* Post Content*/\r\n.post-header {\r\n    width: 100%;\r\n    height: 500px;\r\n    background: var(--container-color);\r\n}\r\n\r\n.post-container {\r\n    max-width: 800px;\r\n    margin: auto;\r\n    width: 100%;\r\n}\r\n\r\n.header-content {\r\n    display: flex;\r\n    flex-direction: column;\r\n    align-items: center;\r\n    margin-top: 4rem !important;\r\n}\r\n\r\n.header-title {\r\n    width: 90%;\r\n    color: var(--bg-color);\r\n    text-align: center;\r\n    margin-bottom: 1rem;\r\n}\r\n\r\n.header-img {\r\n    width: 100%;\r\n    height: 420px;\r\n    object-fit: cover;\r\n    object-position: center;\r\n}\r\n\r\n.post-content {\r\n    max-width: 800px;\r\n    margin:auto;\r\n    width: 90%;\r\n    /* margin-top: 2rem !important; */\r\n}\r\n\r\n.sub-heading {\r\n    font-size: 1.2rem;\r\n}\r\n\r\n.post-text {\r\n    font-size: 1rem;\r\n    line-height: 1.7rem;\r\n    margin: 1rem 0;\r\n    text-align: justify;\r\n}\r\n\r\n/* About Page */\r\n\r\n.profile {\r\n    padding-bottom: 100px;\r\n}\r\n\r\n.headline-tentang {\r\n    font-size: 2rem;\r\n    font-weight: 600;\r\n    text-align: center;\r\n    margin-top: 24px;\r\n    color: var(--container-color);\r\n}\r\n\r\n.cards{\r\n    display: grid;\r\n    transition: 1s;\r\n    grid-template-columns: repeat(auto-fit, minmax(280px, auto));\r\n    justify-content: center;\r\n    gap: 1.5rem;\r\n    padding-top: 45px;\r\n}\r\n\r\n.main-card .cards .card{\r\n    background: #fff;\r\n    border-radius: 12px;\r\n    padding: 30px;\r\n    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.25);\r\n    transition: all 0.4s ease;\r\n}\r\n.main-card .cards .card:hover{\r\n    transform: translateY(-15px);\r\n}\r\n.cards .card .content{\r\n    width: 100%;\r\n    display: flex;\r\n    flex-direction: column;\r\n    justify-content: center;\r\n    align-items: center;\r\n    text-align: center;\r\n}\r\n.cards .card .content .img{\r\n    height: 130px;\r\n    width: 130px;\r\n    border-radius: 50%;\r\n    padding: 3px;\r\n    background: var(--container-color);\r\n    margin-bottom: 14px;\r\n}\r\n.card .content .img img{\r\n    height: 100%;\r\n    width: 100%;\r\n    border: 3px solid #ffff;\r\n    border-radius: 50%;\r\n    object-fit: cover;\r\n}\r\n.card .content .name{\r\n    font-size: 20px;\r\n    font-weight: 500;\r\n}\r\n.card .content .job{\r\n    font-size: 20px;\r\n    color: var(--container-color);\r\n}\r\n.card .content .media-icons{\r\n    margin-top: 10px;\r\n    display: flex;\r\n}\r\n\r\n.media-icons i {\r\n    margin: 14px;\r\n}\r\n.media-icons a{\r\n    text-align: center;\r\n    line-height: 33px;\r\n    height: 35px;\r\n    width: 35px;\r\n    margin: 0 4px;\r\n    color: #FFF;\r\n    border-radius: 50%;\r\n    background: var(--container-color);\r\n    transition: all 0.3s ease;\r\n}\r\n.media-icons a:hover{\r\n    color: var(--container-color);\r\n    background-color: #fff;\r\n    border-color: var(--container-color);\r\n}\r\n.container .button{\r\n    width: 100%;\r\n    display: flex;\r\n    justify-content: center;\r\n    margin: 20px;\r\n}\r\n.button label{\r\n    height: 15px;\r\n    width: 15px;\r\n    border-radius: 20px;\r\n    background: #fff;\r\n    margin: 0 4px;\r\n    cursor: pointer;\r\n    transition: all 0.5s ease;\r\n}\r\n\r\n/* Tab */\r\n\r\n#select-tab{\r\n    width: 100%;\r\n    min-height: 200px;\r\n    padding: 0 0 40px;\r\n}\r\n\r\n.select-tab{\r\n    width: 100%;\r\n    min-height: 200px;\r\n    display: flex;\r\n    transition: 1s;\r\n    justify-content: center;\r\n    gap: 3rem;\r\n    font-size: 30px;\r\n    padding: 40px;\r\n    height: 100%;\r\n}\r\n\r\n.tab-btn {\r\n    text-align: center;\r\n    line-height: 45px;\r\n    height: 4rem;\r\n    width: 4rem;\r\n    margin: 0 4px;\r\n    color: #FFF;\r\n    border-radius: 50%;\r\n    background-color: var(--container-color);\r\n    transition: all 0.3s ease;\r\n}\r\n\r\n.tab-btn p {\r\n    font-size: 14px;\r\n    font-weight: 300;\r\n    display: inline-block;\r\n    color: var(--container-color);\r\n}\r\n\r\n.tab-btn i {\r\n    display: inline-block;\r\n    margin-top: 16px;\r\n}\r\n\r\n.tab-btn :hover {\r\n    transform: scale(1.5);\r\n}\r\n\r\n/* Like */\r\n\r\n.like {\r\n    width: 55px;\r\n    height: 55px;\r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: center;\r\n    background-color: #3e4587;\r\n    \r\n    position: fixed;\r\n    bottom: 16px;\r\n    right: 16px;\r\n    border-radius: 50%;\r\n    border: 0;\r\n    \r\n    font-size: 18px;\r\n    color: white;\r\n    cursor: pointer;\r\n  }\r\n\r\n  #empty-content{\r\n    text-align: center ;\r\n    height: 300px;\r\n  }"],"sourceRoot":""}]);
